import React, { FC, useEffect } from 'react';
import { useSelector } from 'react-redux';
import {
  ACTION_EVENTS,
  alertManager,
  AlertType,
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  enumFormatter,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { CLASS_NAME, PlanoContaLalurA } from '../../../../models/dtos/mcg/planoContaLalurA';
import { LUCRO_REAL_ENUM_VALUES } from '../../../../models/enumerated/mcg/lucroRealEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const PlanoContaLalurASearchView: FC<SearchViewProps<PlanoContaLalurA>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'tipoLancamento',
      editable: false,
      formatExtraData: LUCRO_REAL_ENUM_VALUES,
      formatter: enumFormatter,
      sort: true,
      text: 'Tipo',
    },
  ];

  useEffect(() => {
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message:
        'Essa funcionalidade será desativada nas próximas versões. ' +
        'Para o novo cálculo do Lucro Real utilize Módulo Contábil -> Lucro Real -> Apuração e Configuração.',
      type: AlertType.INFORMATION,
    });
  }, []);

  return (
    <WindowComponent
      title="Manutenção de Plano de Contas do Lalur Parte A"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'PlanoContaLalurAEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default PlanoContaLalurASearchView;
